body {
  font-family: sans-serif;
  margin: 0;
  background: #f4f4f4;
  color: #333;
}

.hero {
  background: #1a1a1a;
  color: white;
  padding: 3rem;
  text-align: center;
}

.cta {
  background: #ff9800;
  padding: 0.5rem 1rem;
  color: white;
  text-decoration: none;
  border-radius: 5px;
}

section {
  padding: 2rem;
  background: white;
  margin: 1rem;
  border-radius: 10px;
}

form input {
  padding: 0.5rem;
  margin-right: 1rem;
  width: 250px;
}

form button {
  padding: 0.5rem 1rem;
  background: #1a1a1a;
  color: white;
  border: none;
}
